@import url("https://fonts.googleapis.com/css2?family=Patua+One&display=swap");

header {
  text-align: center;
}
header h1 {
  font-family: "Patua One", cursive;
}
header img {
  margin: 0px 20px;
}

h2 {
  font-size: 50px;
  font-weight: bold;
  font-family: "Patua One", cursive;
}

section {
  width: 80%;
  text-align: center;
  margin: 15% auto 0px auto;
}

.error-msg {
  color: #ff0104;
}

.response span {
  font-size: 50px;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 10px 0px;
  text-align: center;
}

footer p small {
  font-family: "Patua One", cursive;
}
